"use client";

import { Document, Page, pdfjs } from "react-pdf";
import "react-pdf/dist/Page/AnnotationLayer.css";
import "react-pdf/dist/Page/TextLayer.css";

pdfjs.GlobalWorkerOptions.workerSrc = `https://unpkg.com/pdfjs-dist@${pdfjs.version}/build/pdf.worker.min.js`;

interface Props {
  fileUrl: string;
  currPage: number;
  onLoadSuccess: (total: number) => void;
}

export default function PDFMainViewer({
  fileUrl,
  currPage,
  onLoadSuccess,
}: Props) {
  return (
    <div className="flex justify-center bg-gray-100 border rounded-lg overflow-hidden min-h-[500px]">
      <Document
        file={`/api/pdf?url=${encodeURIComponent(fileUrl)}`}
        onLoadSuccess={({ numPages }) => onLoadSuccess(numPages)}
        loading={
          <div className="h-96 flex items-center justify-center">
            Đang tải PDF...
          </div>
        }
        error={
          <div className="h-96 flex items-center justify-center text-red-500">
            Không thể tải PDF
          </div>
        }
      >
        <Page pageNumber={currPage} width={800} />
      </Document>
    </div>
  );
}
